@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

.App{
    font-family: 'Poppins', sans-serif;
}
.header{
    display: flex;
    flex-direction: column;
    margin-bottom: 150px;
    
}

.header1{
    display:flex;
    justify-content: flex-end;
    padding: 30px;
    background-color:  transparent;
    position: absolute;
    font-size: .8em;
    margin-top: 0;
    margin-left: -60px;
    width: 100%;
    height: 20px; 
    align-items: center;

}
.header2{
    background-image: linear-gradient(rgba(0, 0, 20, 0.7), rgba(0, 0, 20, 0.7)), url(https://images.unsplash.com/photo-1487017159836-4e23ece2e4cf?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=751&q=80);
    background-size: 100% 130%;
    height: 800px;
    width: 100%;
    margin-top:  0px;
    margin-bottom: 0;
}
.button{
    opacity: .7;
    background-color: rgb(0,128,128);
    color: white;
   width: 200px;
   margin: auto;
   margin-top: -250px;
   margin-bottom: 190px;
   text-align: center;
   border-radius: 50px;
}
.button:hover{
    opacity: 1;
    text-transform: uppercase;
}

.h1{
    text-align: center;
    margin-top: 200px;
    font-size: 3.5em;
    color:  white;
    font-style: normal;
    font-weight: normal;
    white-space: nowrap;
    overflow: hidden;
}
.p{
    text-align: center;
    margin-left: 0px;
    margin-top: -30px;
    color:  white;
    font-size: 2em; 
    white-space: nowrap;
    overflow: hidden;
}


a {
      opacity: .8;
    color: #fff;
    text-decoration: none;
    letter-spacing: 0.15em;
    display: inline-block;
    padding: 15px 20px;
    position: relative;
    font-size: 1.2em;
}
a:after {    
    background: none repeat scroll 0 0 transparent;
    bottom: 0;
    content: "";
    display: block;
    height: 5px;
    left: 50%;
    position: absolute;
    background: #fff;
    transition: width 0.3s ease 0s, left 0.3s ease 0s;
    width: 0;
    transform: scale(.7);
}
.ahvr:hover{
    opacity: 1;
      
}
.ahvr:hover:after { 
    width: 100%; 
    left: 0;
}
 
.aboutme{
    width: 700px;

}
  .profilepic{
      width: auto;
    height: 500px;
    border-radius: 80px;
    margin-right: 40px;
  }
  .profilepic:hover{
      transform: scale(1.02);
  }
  .about{
    display: flex;
    justify-content: space-around;
    font-style: normal;
    font-weight: normal;
    padding-top: 110px;
    padding-left: 50px;
    margin-top: 50px;
    height: 600px;     
  }
.text1{
    color: teal;
    font-size: 3em;
}
.text2{
    color: teal;
    font-size: 1.5em;
    width: 500fpx;
}
.project{
    display: flex;
    flex-direction: row-reverse;
    height: 500px;
    background-color: white;
    margin-top: -160px;
    padding: 50px;
